/*=====================
    01. Dark CSS start
==========================*/
body {
  &.dark {
    background-color: $dark-body;
    transition: all 0.3s ease;
    color: $white-5;
  }
}
.dark {
  h1 {
    color: $white;
  }
  h2 {
    color: $white-1;
  }
  h3 {
    color: $white-2;
  }
  h4 {
    color: $white-3;
  }
  h5 {
    color: $white-4;
  }
  h6 {
    color: $white-5;
  }
  p {
    color: $white-5;
  }
  li {
    color: $white-5;
  }
  a {
    color: $dark-link;
  }
  .page-link {
    background-color: $dark-body;
    transition: all 0.3s ease;
    border-color: $dark-border;
  }
  .breadcrumb-section {
    background-color: $dark-top;
    transition: all 0.3s ease;
  }
  .electronic-footer {
    .light-layout {
      background-color: $dark-body;
    }
  }
  .light-layout {
    background-color: $dark-top;
    transition: all 0.3s ease;
  }
  .form-control {
    color: $white-5;
    background-color: $dark-body;
    transition: all 0.3s ease;
    border: 1px solid $dark-border;
  }
  .btn-solid {
    color: $white !important;
    &:hover {
      color: $dark-font !important;
    }
  }
  select {
    color: $white-5;
  }
  option {
    background-color: $dark-body;
    transition: all 0.3s ease;
  }
  .header-tools {
    .top-header {
      background-color: transparent;
    }
  }
  .main-menu{
    .menu-left{
      .brand-logo{
        img{
          filter: brightness(0.5);
        }
      }
    }
  }
  .form_search{
    box-shadow: 0 0 0 1px $dark-border;
  }
  .footer-theme2{
    .footer-brand-logo{
      img{
        filter: brightness(0.5);
      }
    }
  }
  .addtocart_count{
    .partition_3{
      .product-box, .product-wrap{
        .img-wrapper{
          .front{
            background-color: $white;
          }
        }
      }
    }
  }
  .form-check-input{
    background-color: $dark-top;
  }
  .product-accordion{
    .card-header{
      border-bottom: 1px dashed $dark-border;
    }
  }
  .game-banner{
    .banner-timer{
      .banner-text{
        h2{
          color: $white;
        }
      }
    }
  }
  .banner-tools{
    border: 1px solid $dark-border;
  }
  .product-right{
    .timer{
      .timer-p{
        color: $white;
      }
    }
  }
  .modal{
    .modal-content{
      .modal-header{
        .close{
          color: $white;
        }
      }
    }
  }
  .product-filter-tags{
    li{
      a{
        border: 1px solid $dark-border;
      }
    }
  }
  .footer-white{
    .sub-footer{
      .row{
        border-top: 1px solid $dark-border;
      }
    }
  }
  .dashboard-section{
    .dashboard-sidebar{
      .profile-top{
        .profile-image{
          img{
            filter: brightness(0) invert(1);
          }
        }
      }
    }
  }
  .full-box{
    .center-slider{
      .product-box, .product-wrap{
        .img-wrapper{
          .front{
            background-color: $white;
          }
        }
      }
    }
  }
  .banner-timer{
    .banner-text{
      h2{
        color: $dark-font;
      }
    }
  }
  .product-box, .product-wrap{
    .img-wrapper{
      .lable-block{
        .lable4{
          color: $dark;
        }
      }
    }
  }
  header {
    background-color: $dark-body;
    transition: all 0.3s ease;
    &.sticky {
      &.fixed {
        background-color: $dark-body;
      }
    }
    &.header-5 {
      .pixelstrap {
        a,
        &:hover,
        &:active {
          color: $dark-font;
        }
      }
    }
    &.header-metro {
      .metro {
        background-color: $dark-top;
        transition: all 0.3s ease;
      }
      .top-header {
        .header-contact {
          li {
            color: $header-font;
          }
        }
        .header-dropdown {
          li {
            color: $header-font;
            a {
              i {
                color: $header-font;
              }
            }
          }
        }
      }
    }
    &.header-tools {
      background-color: transparent;
      transition: all 0.3s ease;
      .logo-menu-part {
        > .container {
          background-color: $dark-body;
          transition: all 0.3s ease;
          border-color: $dark-border;
        }
      }
    }
    &.left-header {
      .top-header {
        background-color: $dark-body;
        transition: all 0.3s ease;
      }
      .main-menu {
        .menu-right {
          .header-dropdown {
            > li {
              img {
                filter: invert(100);
              }
            }
          }
        }
      }
      .onhover-div {
        > div {
          img {
            filter: invert(100);
          }
        }
      }
      .sidenav {
        nav {
          background-color: $dark-top;
          transition: all 0.3s ease;
        }
        .left-sidebar_center {
          background-color: $dark-top;
          transition: all 0.3s ease;
        }
        .pixelstrap {
          > li {
            > a {
              background-color: $dark-top;
              transition: all 0.3s ease;
            }
          }
        }
      }
    }
    &.green-gradient {
      background-color: $dark-body;
      transition: all 0.3s ease;
      .top-header {
        background-color: $dark-top;
        transition: all 0.3s ease;
        background: $dark-top;
        background-image: none;
      }
    }
  }
  .main-menu{
    .brand-logo{
      &.layout2-logo{
        a{
          img{
            filter: brightness(0.5);
          }
        }
      }
    }
  } 
  .footer-theme{
    .footer-logo{
      a{
        img{
          filter: brightness(0.5);
        }
      }
    }
  } 
  .left-header {
    .sidenav {
      .leftside_social {
        background-color: $dark-top;
      }
    }
  }
  .add_to_cart {
    &.top,
    &.bottom {
      .cart-inner {
        background-color: $dark-body;
        .cart_top {
          background-color: $dark-top;
        }
      }
    }
    .cart-inner {
      background-color: $dark-body;
      .cart_top {
        border-color: $dark-border;
        h3 {
          color: $white-2;
        }
        .close-cart {
          i {
            color: $white;
          }
        }
      }
      .cart_media {
        li {
          .media {
            .media-body {
              h4 {
                color: $white-3;
              }
            }
          }
          .total {
            border-color: $dark-border;
          }
        }
      }
    }
  }
  .grey-bg {
    background-color: $dark-top;
  }
  .sticky {
    &.fixed {
      box-shadow: 0px 0px 8px #403535;
    }
  }
  .top-header {
    background-color: $dark-top;
    transition: all 0.3s ease;
  }
  .sub-footer {
    background-color: $dark-body;
    transition: all 0.3s ease;
  }
  .border-section {
    border-color: $dark-border;
  }
  .main-navbar {
    .nav-menu {
      .back-btn {
        .mobile-back {
          color: $white-1;
        }
      }
      > li {
        > a {
          color: $white-1;
        }
        .nav-submenu {
          background-color: $dark-body;
          box-shadow: none;
          li {
            a {
              color: $white-1;
            }
            .nav-sub-childmenu {
              background-color: $dark-body;
              box-shadow: none;
              li {
                a {
                  color: $white-1;
                }
              }
            }
          }
        }
        .mega-menu-container {
          background-color: $dark-body;
          box-shadow: none;
          .mega-box {
            .link-section {
              .menu-content {
                ul {
                  li {
                    a {
                      color: $white-1;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .home-slider {
    &.fullpage {
      .home {
        .slider-contain {
          p {
            color: $grey;
          }
        }
      }
    }
  }
  .addtocart_count {
    .product-box {
      .add-button {
        background-color: $dark-top;
        color: $white;
      }
    }
  }
  .tools-brand {
    background-color: $dark-body;
  }
  .sm-vertical {
    background-color: $dark-top;
    transition: all 0.3s ease;
  }
  .sidenav {
    nav {
      background-color: $dark-top;
      transition: all 0.3s ease;
      .sidebar-menu {
        background-color: $dark-top;
        li {
          a {
            color: $white-1;
          }
          .mega-menu {
            background-color: $dark-top;
            box-shadow: none;
            .link-section {
              ul {
                background-color: $dark-top;
              }
            }
          }
          ul {
            background-color: $dark-top;
          }
        }
      }
    }
    .sidebar-back {
      color: $white-1;
      border-color: $dark-border;
    }
  }
  .pixelstrap {
    ul {
      background: $dark-top;
      box-shadow: none;
      a {
        color: $white-3;
        &:hover,
        &:focus,
        &:active,
        &.highlighted {
          color: $white-3;
        }
      }
    }
  }
  .pixelstrap {
    .home-menu,
    .feature-menu,
    .category-menu,
    .full-mega-menu,
    .clothing-menu {
      box-shadow: 0 0 1px 0 $dark-body;
    }
  }
  .onhover-div {
    > div {
      img {
        filter: brightness(100);
      }
    }
    .show-div {
      background-color: $dark-top;
      transition: all 0.3s ease;
      box-shadow: none;
    }
  }
  .error-section {
    h1 {
      color: #e6e6e6;
    }
  }
  .breadcrumb-item {
    &.active {
      color: $white-3;
    }
  }
  .breadcrumb-section {
    .breadcrumb {
      a {
        color: $grey-about;
      }
    }
  }
  .main-menu {
    .menu-left {
      .navbar {
        i {
          color: $grey-about;
        }
      }
    }
    .menu-right {
      .icon-nav {
        .onhover-div {
          .show-div {
            &.setting {
              h6 {
                color: $white-5;
              }
              ul {
                li {
                  a {
                    color: $white-5;
                  }
                }
              }
            }
            &.shopping-cart {
              li {
                .media {
                  .media-body {
                    h4 {
                      color: $white-3;

                      span {
                        color: $white-4;
                      }
                    }
                  }
                }
                .close-circle {
                  i {
                    &:hover {
                      color: $white;
                    }
                  }
                }
                .total {
                  border-color: $dark-border;
                  h5 {
                    color: $white-4;
                  }
                }
                .buttons {
                  a {
                    color: $dark-link;
                    &:hover {
                      color: var(--theme-deafult);
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .footer-theme {
    .sub-title {
      li {
        color: $white-5;
        a {
          color: $white-5;
        }
      }
      h4 {
        color: $white-3;
      }
    }
  }
  .footer-social,
  .social-white {
    i {
      color: $white-5;
    }
  }
  .footer-light {
    .subscribe {
      border-color: $dark-border;
    }
  }
  .footer-theme2 {
    .contact-details {
      li {
        color: $white-5;
        a {
          color: $white-3;
        }
      }
    }
    .footer-link,
    .footer-link-b {
      a {
        color: $dark-link;
        &:hover {
          color: $white;
        }
      }
    }
  }
  .top-header {
    .header-contact {
      li {
        color: $white-5;
      }
    }
    .header-dropdown {
      li {
        color: $white-5;
        a {
          i {
            color: $white-5;
          }
        }
      }
      .onhover-dropdown {
        .onhover-show-div {
          li {
            a {
              color: $dark-link;
            }
          }
        }
      }
    }
    &.top-header-dark {
      .header-dropdown {
        li {
          a {
            color: $dark-link;
          }
        }
      }
      .header-contact {
        li {
          i {
            color: $dark-link;
          }
        }
      }
    }
    &.top-header-dark2 {
      .header-dropdown {
        li {
          a {
            color: $white-5;
          }
        }
      }
      .header-contact {
        li {
          i {
            color: $white-5;
          }
        }
      }
    }
    &.top-header-dark3 {
      .header-dropdown {
        li {
          a {
            color: $dark-link;
          }
        }
      }
      .header-contact {
        li {
          i {
            color: $dark-link;
          }
        }
      }
    }
  }
  .onhover-dropdown {
    .onhover-show-div {
      background-color: $dark-top;
      transition: all 0.3s ease;
      box-shadow: none;
    }
  }
  .testimonial {
    background-color: $dark-top;
    transition: all 0.3s ease;
    .testimonial-slider {
      .slick-track {
        .slick-slide {
          &:nth-child(even) {
            .media {
              border-color: $dark-border;
            }
          }
        }
      }
      .media {
        img {
          border-color: $dark-border;
        }
      }
    }
  }
  .team {
    h2 {
      border-color: $dark-border;
    }
  }
  .service-block {
    + .service-block {
      border-color: $dark-border;
    }
  }
  .pixelstrap {
    &.light-font-menu {
      li {
        > a {
          color: $white-1;
        }
      }
    }
  }
  .theme-tab {
    .tab-title,
    .tab-title2 {
      a {
        color: $dark-link;
      }
      .current {
        a {
          color: var(--theme-deafult);
        }
      }
    }
    .tab-title2 {
      &:after {
        border-color: $dark-border;
      }
      .current {
        background-color: $dark-top;
        transition: all 0.3s ease;
        a {
          background-color: $dark-top;
          transition: all 0.3s ease;
        }
      }
    }
  }
  .product-box,
  .product-wrap {
    .product-detail,
    .product-info {
      h4 {
        color: $white-3;
      }
    }
  }
  .theme-card {
    .offer-slider {
      .media {
        .media-body {
          h4 {
            color: $white-3;
          }
        }
      }
    }
    .slick-prev,
    .slick-next {
      &:before {
        color: $white;
      }
    }
  }
  .theme-card {
    h5 {
      &.title-border {
        border-color: $dark-border;
      }
    }
    &.card-border {
      border-color: $dark-border;
    }
  }
  .dark-layout {
    background-color: $dark-body;
    transition: all 0.3s ease;
  }
  .sub-footer {
    &.darker-subfooter {
      background-color: $dark-top;
      transition: all 0.3s ease;
      p {
        color: $white-5;
      }
    }
  }
  .collection-product-wrapper{
    .product-top-filter{
      .popup-filter{
        .open-popup{
          &.open{
            .theme-card{
              background-color: $left-sidebar;
              border: 1px solid $dark-border;
              border-top: 0;
            }
          }
        }
      }
    }
  }
  .search-overlay {
    background-color: $dark-body;
  }
  .blog-page {
    .blog-media {
      .blog-right {
        h4 {
          color: $white-3;
        }
        ul {
          color: $white-5;
        }
      }
    }
    .blog-sidebar {
      .theme-card {
        background-color: $dark-body;
        transition: all 0.3s ease;
        border-color: $dark-border;
      }
      h4 {
        color: $white-3;
      }
      h6 {
        color: $white-5;
      }
      p {
        color: $white-5;
      }
    }
  }
  .blog-section {
    .blog-details {
      p {
        color: $white;
      }
    }
    .review-box {
      background-color: $dark-body;
      .review-content {
        h6 {
          color: $white-5;
        }
      }
    }
  }
  .blog-detail-page {
    .blog-detail {
      h3 {
        color: $white-2;
      }
      p {
        color: $white-5;
      }
    }
    .post-social {
      color: $white-5;
      border-color: $dark-border;
      li {
        + li {
          border-color: $dark-border;
        }
      }
    }
    .comment-section {
      border-color: $dark-border;
      li {
        border-color: $dark-border;
        h6 {
          color: $white-5;
          span {
            color: $dark-span;
          }
        }
      }
    }
    .blog-contact {
      .theme-form {
        label {
          color: $white-5;
        }
        input,
        textarea {
          background-color: $dark-body;
          transition: all 0.3s ease;
          border-color: $dark-border;
        }
      }
    }
  }
  .cart-section,
  .wishlist-section {
    .cart-table {
      thead {
        th {
          color: $white-1;
        }
      }
    }
    tbody {
      tr {
        td {
          border-color: $dark-border;
          a {
            color: $dark-link;
          }
          h2 {
            color: $white-1;
          }
        }
      }
    }
  }
  .table {
    tbody {
      + tbody {
        border-color: $dark-border;
      }
    }
    thead {
      th {
        border-color: $dark-border;
      }
    }
  }
  .top-banner-wrapper {
    .top-banner-content {
      h4 {
        color: $white-3;
      }
      h5 {
        color: $white-4;
      }
      p {
        color: $white-5;
      }
    }
  }
  .collection-product-wrapper {
    .product-top-filter {
      border-color: $dark-border;
      .product-filter-content,
      .popup-filter {
        .search-count,
        .sidebar-popup {
          border-color: $dark-border;
        }
        .collection-grid-view {
          border-color: $dark-border;
        }
        .product-page-per-view {
          select {
            border-color: $dark-border;
          }
        }
      }
      .popup-filter {
        .sidebar-popup {
          a {
            color: $dark-link;
          }
        }
      }
    }
  }
  .product-pagination {
    border-color: $dark-border;
    .pagination {
      .page-item {
        &.active {
          a {
            background-color: $dark-top;
            transition: all 0.3s ease;
            border-color: $dark-border;
          }
        }
      }
    }
    .product-search-count-bottom {
      border-color: $dark-border;
      h5 {
        color: $white-4;
      }
    }
  }
  .portfolio-section {
    &.metro-section {
      .product-box {
        .product-detail {
          background-color: $dark-body;
          transition: all 0.3s ease;
          h4 {
            color: $white-3;
          }
          h6 {
            color: $white-5;
          }
        }
        .cart-wrap {
          button {
            border-color: $dark-border;
            background-color: rgba($dark-body, 0.8);
            transition: all 0.3s ease;
            i {
              color: $dark-link;
            }
          }
          a {
            i {
              border-color: $dark-border;
              background-color: rgba($dark-body, 0.8);
              transition: all 0.3s ease;
              color: $dark-link;
            }
          }
        }
      }
    }
  }
  .collection-filter-block {
    border-color: $dark-border;
    background-color: $dark-body;
    transition: all 0.3s ease;
  }
  .collection-collapse-block {
    .collapse-block-title {
      color: $white-2;
      &:after {
        color: $white-2;
      }
    }
    .collection-collapse-block-content {
      .collection-brand-filter {
        .collection-filter-checkbox {
          label {
            color: $dark-span;
          }
        }
      }
    }
  }
  .load-more-sec {
    a {
      border-color: $dark-border;
    }
  }
  .checkout-page {
    .checkout-title {
      h3 {
        color: $white-2;
      }
    }
    .checkout-form {
      .form-group {
        .field-label {
          color: $white-5;
        }
      }
      input {
        &[type="text"],
        &[type="email"],
        &[type="password"],
        &[type="tel"],
        &[type="number"],
        &[type="url"] {
          background-color: $dark-body;
          transition: all 0.3s ease;
          border-color: $dark-border;
        }
      }
      select,
      textarea {
        border-color: $dark-border;
      }
      .checkout-details {
        background-color: $dark-top;
        transition: all 0.3s ease;
        border: 1px solid $dark-border;
      }
    }
  }
  .order-box {
    .title-box {
      color: $white-2;
      border-color: $dark-border;
    }
    .qty {
      border-color: $dark-border;
      li {
        color: $white-5;
        span {
          color: $white-4;
        }
      }
    }
    .sub-total {
      border-color: $dark-border;
      li {
        color: $white-5;
      }
      .shopping-option {
        label {
          color: $dark-span;
        }
      }
    }
    .total {
      li {
        color: $white-5;
      }
    }
  }
  .payment-box {
    .payment-options {
      li {
        .radio-option {
          label {
