/*=====================
    04. RTL CSS start
==========================*/
body.rtl {
  direction: rtl;
}
// general css
.rtl {
  &.dark{
    .blog-detail-page{
      .blog-detail{
        .post-social{
          li{
            + li{
              border-right: 1px solid $dark-border;
            }
          }
        }
      }
    }
    .tab-border{
      .nav-border{
        border-right: unset;
        border-left: 1px solid $dark-border;
      }
    } 
  }
  .slick-slider {
    direction: ltr;
  }
  .slick-slide {
    float: left;
  }
  ul {
    -webkit-padding-start: 0;
  }
  .ps-2{
    padding-left: 0 !important;
    padding-right: 0.5rem !important;
  }
  .pe-2,
  .px-2 {
    padding-right: 0 !important;
    padding-left: 0.5rem !important;
  }
  .me-1,
  .mx-1 {
    margin-left: 0.25rem !important;
    margin-right: 0 !important;
  }
  .me-2,
  .mx-2 {
    margin-right: 0 !important;
    margin-left: 0.5rem !important;
  }
  .ms-2,
  .mx-2 {
    margin-left: 0 !important;
    margin-right: 0.5rem !important;
  }
  .me-3,
  .mx-3 {
    margin-right: 0 !important;
    margin-left: 1rem !important;
  }
  .ms-3,
  .mx-3 {
    margin-left: 0 !important;
    margin-right: 1rem !important;
  }
  .ms-auto,
  .mx-auto {
    margin-left: unset !important;
    margin-right: auto !important;
  }
  .pe-0,
  .px-0 {
    padding-right: unset !important;
    padding-left: 0 !important;
  }
  .text-end {
    text-align: left !important;
  }
  .text-start {
    text-align: right !important;
  }
  .form-select{
    padding: 0.375rem 0.75rem 0.375rem 2.25rem;
    background-position: left 0.75rem center;
  }
  .theme-form {
    .form-group {
      text-align: right;
    }
  }
}
.rtl {
  .snow-slider {
    .slick-slide {
      img {
        width: 100%;
      }
    }
  }
  .effect-cls {
    &:before,
    &:after {
      right: 0;
    }
  }
  .left-header {
    .sidenav {
      .pixelstrap {
        > li {
          > a {
            text-align: right;
          }
        }
        li {
          ul {
            li {
              a {
                text-align: right;
              }
            }
          }
        }
      }
    }
  }
  .christmas{
    .blog-section.grey-bg{
      .slide-3{
        .slick-slide{
          .blog-details{
            text-align: right;
          }
        }
      }
    }
  }
  .offer-box{
    right: -415px;
    left: unset;
    transform: scale(-1);
    .heading-right{
      h3{
        top: 100%;
      }
    }
  }
  .dashboard-section{
    .counter-section{
      .counter-box{
        border-left: unset;
        border-right: 3px solid var(--theme-deafult);
      }
    }
  }  
  .payment-box{
    .payment-options{
      li{
        .radio-option{
          label{
            img{
              margin-left: unset;
              margin-right: -30px;
            }
          }
        }
      }
    }
  }
  .dashboard-section{
    .dashboard-box{
      .dashboard-detail{
        .account-setting{
          .row{
            > div{
