      padding-left: 0.5rem !important;
      padding-right: 0 !important;
    }
  }  
  .tab-border{
    .nav-border{
      border-right: unset;
      border-left: 1px solid $round-border;
    }
  } 
  .theme-card{
    .offer-slider{
      .media{
        .media-body{
          .rating{
            i{
              padding-left: 5px;
              padding-right: unset;
            }
          }
        }
      }
    }
  }
  .collection-mobile-back{
    span{
      i{
        margin-right: unset;
        margin-left: 5px;
      }
    }
  }
  .collection-product-wrapper {
    .product-top-filter {
      .product-filter-content, .popup-filter {
        .collection-grid-view {
          border-right: 0;
          border-left: 1px solid $round-border;
          ul {
            li {
              margin-left: unset;
              margin-right: 10px
            }
          }
        }
      }
    }
  }
  
  .color-selector{
    ul{
      li{
        margin-right: unset;
        margin-left: 5px;
      }
    }
  }
  .load-more{
