      }
    }
  }
  .top-header {
    .header-contact {
      li {
        padding-right: 0;
        padding-left: 25px;
      }
    }
  }
  .onhover-dropdown {
    &:before {
      left: 3px;
      right: unset;
    }
  }
  .sticky.fixed{
    .cart-qty-cls{
      right: unset;
      left: -11px;
    }
  } 
  .top-header{
    .header-dropdown{
      li{
        padding-right: 20px;
      }
    }
  }
  .setting-box{
    .buy_btn{
      a{
        i, img{
          margin-right: unset;
          margin-left: 5px;
        }
      }
    }
  }
  .setting-box{
    .setting-contant{
      .setting_buttons{
        text-align: left;
      }
    }
    .setting-title{
      text-align: left;
    }
  }
  .main-menu{
    .menu-right{
      .icon-nav{
        .onhover-div{
          .show-div.shopping-cart{
            li{
              .media{
                .media-body{
                  h4{
                    text-align: right;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .top-header {
    .header-dropdown {
      .onhover-dropdown{
        .onhover-show-div{
          li{
            padding-left: unset;
            padding-right: 0;
          }
        }
      }
      > li {
        &:nth-child(2) {
          padding: 15px 25px;
          padding-left: 0;
        }
      }
      li {
        i {
          padding-right: 0;
          padding-left: 5px;
        }
