    }
    .header-contact {
      text-align: right;
      li {
        i {
          padding-right: 0;
          padding-left: 10px;
        }
      }
    }
  }
  .main-menu {
    .menu-left {
      .navbar {
        padding: 40px 0 40px 45px;
      }
    }
    .menu-right {
      float: left;
      .icon-nav {
        li {
          padding-left: 0;
          padding-right: 20px;
