  header {
    .main-navbar {
      .nav-menu {
        > li {
          float: right;
          > a {
            padding-right: 0;
            padding-left: 45px;
            .sub-arrow {
              right: auto;
              left: 7px;
            }
          }
          .mega-menu-container {
            .mega-box {
              text-align: right;
              .link-section {
                .menu-content {
                  ul {
                    li {
                      a {
                        &:before {
                          right: 0;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
          .nav-submenu {
            text-align: right;
            li {
              a {
                i {
                  float: left;
                  &::before{
                    content: "\f104";
                  }
                }
              }
              .nav-sub-childmenu {
                left: -200px;
                right: unset;
              }
            }
          }
        }
      }
    }
    .sidenav {
      .sidebar-menu {
        li {
          direction: rtl;
          text-align: right;
        }
      }
    }
    &.left-header {
      .sidenav {
        .pixelstrap {
          li {
            .mega-menu-container {
              right: 0px;
              margin-right: 270px;
              left: unset;
              margin-left: unset;
            }
            .nav-submenu {
              right: 0px;
              margin-right: 270px;
              margin-left: 0;
              left: unset;
              li {
                .nav-sub-childmenu {
                  left: unset;
                  margin-left: 0;
                  right: 0px;
                  margin-right: 228px;
                }
                a {
                  i {
                    float: left;
                  }
                }
              }
            }
          }
        }
        .left-sidebar_center {
          padding: 40px 30px 30px 0;
          .pixelstrap {
            li {
              a {
                .sub-arrow {
                  left: 7px;
                  right: unset;
                }
