            margin-right: unset !important;
            margin-left: 10px !important;
          }
          &:nth-of-type(even){
            margin-left: unset !important;
            margin-right: 10px !important;
          }
        }
      }
    }
    .banner-timer{
      .timer{
        padding-right: 20px;
        padding-left: unset;
      }
    } 
    .footer-title {
      h4 {
        text-align: right;
      }
      .according-menu {
        &:before {
          left: 2px;
          right: unset;
        }
      }
      &.active {
        .according-menu {
          &:before {
            left: 2px;
            right: unset;
          }
        }
      }
    }
    .footer-contant {
      text-align: right;
    }
    .footer-theme {
      .sub-title {
        li {
          text-align: right;
        }
        .contact-list {
          li {
            padding-right: 0;
          }
          i {
            margin-right: 0;
            margin-left: 10px;
          }
        }
      }
    }
    .service-block {
      .media {
        .media-body {
          text-align: center;
        }
      }
      + .service-block {
        border: none;
      }
    }
    .footer-theme2 {
      .contact-details {
        li {
          text-align: right;
        }
      }
    }
    .social-white {
      li {
        &:first-child {
          padding-right: 0;
        }
      }
    }
    .product-box,
    .product-wrap {
      .cart-detail {
        left: 10px;
        right: unset;
      }
    }
    .footer-theme2 {
      .footer-link,
      .footer-link-b {
        li {
          padding-left: 0;
          padding-right: 20px;
        }
      }
    }
    .layout2-logo {
      padding-left: 70px;
      padding-right: 0;
    }
    .testimonial {
      .testimonial-slider {
        .media {
          .media-body {
            padding: 30px 30px 30px 30px;
          }
        }
      }
    }
    .footer-social {
      li {
        padding: 0 15px;
      }
    }
    .service-block {
      svg {
        margin-left: 0;
        margin-bottom: 20px;
      }
    }
  }
}
@media (max-width: 577px) {
  .rtl {
    header {
      &.header-gym{
        .main-menu{
          .brand-logo{
            left: unset;
            right: 0;
          }
        }
      }
      &.left-header {
        .main-menu {
          .menu-left {
            .mobile-logo {
              left: unset;
              right: 15px;
            }
            .navbar {
              right: unset;
              left: 15px;
            }
          }
        }
      }
      &.header-5 {
        .main-menu {
          .brand-logo {
            left: unset;
            right: 0;
          }
        }
      }
    }
    .theme-card.card-border{
      .slick-next{
        left: 38px;
      }
      .slick-prev{
        left: 16px;
      }
    } 
    .theme-card.card-border{
      h5{
        padding-right: 20px;
        padding-left: unset;
      }
    }
    .layout3-menu{
      .main-menu{
        > div{
          &:nth-child(2){
            right: 46%;
          }
        }
      }
    }
    .header-gym{
      .brand-logo{
        margin-left: -50%;
        margin-right: unset;
      }
    }
    .full-banner.feature-banner{
      .feature-object{
        li{
          .media{
            .media-body{
              text-align: right;
            }
          }
        }
      }
    }
    .search-overlay{
      > div{
        .closebtn{
          left: 15px;
        }
      }
    }
    .top-header {
      .header-dropdown {
        .mobile-account,
        .mobile-wishlist {
          padding: 0;
        }
        > li {
          &:nth-child(2) {
            padding: 0;
          }
        }
        li {
          padding: 0 !important;
        }
      }
    }
    .main-menu {
      .menu-left {
        .navbar {
          padding: 30px 0 30px 45px !important;
        }
      }
      .menu-right {
        .icon-nav {
          li {
            padding-right: 0;
          }
          .mobile-cart {
            right: unset;
            left: 48%;
          }
        }
      }
    }
    .main-nav-center {
      .toggle-nav {
        right: unset;
        left: 15px;
      }
    }
    .layout2-logo {
      padding-left: 0;
    }
    .layout3-menu {
      .main-menu {
        .menu-left {
          .menu-right {
            .toggle-nav {
              left: 15px;
              right: unset;
            }
          }
        }
      }
    }
    .collection-product-wrapper {
      .product-top-filter {
        .product-filter-content {
          .product-page-per-view {
            border-right: none !important;
          }
          .product-page-filter {
            border-right: 1px solid $round-border !important;
          }
        }
      }
    }
    .blog-detail-page {
      .blog-detail {
        .post-social {
          li {
            + li {
              padding-right: 0;
              margin-right: 0;
              border-right: none;
            }
          }
        }
      }
    }
    .full-banner {
      &.feature-banner {
        .feature-object {
          text-align: right;
          li {
            .media {
              img {
                margin-left: 15px;
                margin-right: 0;
              }
            }
          }
        }
      }
    }
  }
}
@media (max-width: 480px) {
  .rtl {
    .testimonial {
      .testimonial-slider {
        .media {
          .media-body {
            text-align: center;
            padding: 6px;
          }
        }
      }
    }
    .review-page {
      .comment-section {
        .media {
          text-align: center;
        }
      }
    }
    .blog-detail-page {
      .comment-section {
        li {
          img {
            margin-left: 0;
          }
        }
      }
    }
    .blog-page {
      .blog-media {
        .blog-right {
          ul {
            li {
              + li {
                padding-right: 0;
                margin-right: 0;
                border-right: none;
              }
            }
          }
        }
      }
    }
  }
}
@media (max-width: 420px) {
  .rtl {
    .full-box {
      .center-slider {
        .offer-slider {
          .product-box {
            direction: rtl;
          }
        }
      }
    }
    .theme-card{
      .offer-slider{
        .media{
          img{
            padding: 0 15px 0;
          }
        }
      }
    }
    .tools-grey.border-box{
      .product-5{
        margin-bottom: 0;
      }
    }
    .product-right.product-form-box{
      .timer{
        padding-left: unset;
        padding-right: 29px;
      }
    } 
    .collection-filter{
      .theme-card{
        .offer-slider{
          .media{
            .media-body{
              .rating{
                i{
                  padding-left: 0;
                }
              }
            }
          }
        }
      }
    }
    .product-right {
      .product-icon {
        .product-social {
          li {
            padding-left: 5px;
          }
        }
      }
    }
    .timer {
      span {
        .padding-l {
          padding-right: 5px;
        }
      }
    }
    .order-box {
      .sub-total {
        .shipping {
          .shopping-option {
            &:last-child {
              padding-left: 0;
              padding-right: 20px;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 360px) {
  .rtl {
    header {
      &.header-metro{
        .main-menu{
          .menu-right{
            .icon-nav{
              .mobile-cart{
                .show-div.shopping-cart{
                  left: -145px !important;
                }
              }
            }
          }
        }    
      }
    }
    .layout3-menu{
      .main-menu{
        > div{
          &:nth-child(2){
            right: 41%;
          }
        }
      }
    }
    .setting-sidebar.open-icon{
      left: 275px;
    }
    .tools-parallax-product.tab-left{
      .theme-tab{
        .tab-content-cls{
          margin-bottom: 0;
        }
      }
    }
    .bg-title{
      .theme-card{
        .slick-next{
          left: 20px;
        }
      } 
    }
    .setting-box{
      .buy_btn{
        a{
          i{
            margin-left: 3px;
            margin-right: unset;
          }
        }
      }
    }
    .timer {
      span {
        .padding-l {
          padding-right: 10px;
        }
      }
    }
  }
}
